.done {
  &__title {color: white;
    margin-bottom: 2rem;
    padding: 1.5rem 0;
    background-color: rgb(0, 128, 117);
    display: flex;
    align-items: center;
    justify-content: center;

    &-icon {
      font-size: 4rem;
      border: 1px solid white;
      border-radius: 50%;
      padding: 0.5rem;
      margin-right: 2rem;
    }

    &-text {
      font-size: 3rem;
      text-transform: uppercase;
    }
  }
}