.failed {
  &__title {color: white;
    margin-bottom: 2rem;
    padding: 1.5rem 0;
    background-color: rgb(128, 41, 0);
    display: flex;
    align-items: center;
    justify-content: center;

    &-icon {
      font-size: 5rem;
      padding: 0.5rem;
      margin-right: 2rem;
    }

    &-text {
      font-size: 3rem;
      text-transform: uppercase;
    }
  }
}